import { useTranslation } from "react-i18next";
import { NavigateFunction, useNavigate } from "react-router-dom";
import { Routing } from "routing";
import { RootState, useShallowEqualSelector } from "store";

const goToPaymentMethods = (navigate: NavigateFunction) => () => {
  navigate(`/${Routing.platformModule}/${Routing.platform.paymentMethods}`);
};

export const GoToPaymentMethodsOption = () => {
  const { planId } = useShallowEqualSelector((state: RootState) => ({
    planId: state?.user?.plan?.id,
  }));

  const navigate = useNavigate();
  const { t } = useTranslation();

  if (!planId) return null;
  return <p onClick={goToPaymentMethods(navigate)}>{t("menu.payment_methods")}</p>;
};
