import { Action } from "@reduxjs/toolkit";
import { get } from "lodash";
import { TFunction } from "react-i18next";
import { toast } from "react-toastify";
import { Routing } from "routing";
import { eventService } from "services";
import { ActionType } from "../reducer";

export const createNewEvent =
  (t: TFunction<"translation", undefined>, dispach: React.Dispatch<Action<ActionType>>) =>
  async () => {
    dispach({ type: ActionType.IS_LOADING });
    try {
      await eventService.createEvent();
      window.location.replace(`/${Routing.platformModule}/${Routing.platform.adminEvent}`);
    } catch (e: unknown) {
      const message = get(e, "response.data.message");
      switch (message) {
        case "another_event_is_running":
          dispach({ type: ActionType.OPEN_EVENT_ERROR_DIALOG });
          break;
        default:
          toast.error(t(message));
          break;
      }
    } finally {
      dispach({ type: ActionType.IS_NOT_LOADING });
    }
  };
