import { Plan } from "enums";
import { useTranslation } from "react-i18next";
import { NavigateFunction, useNavigate } from "react-router-dom";
import { Routing } from "routing";
import { RootState, useShallowEqualSelector } from "store";

const goToUsers = (navigate: NavigateFunction) => () => {
  navigate(`/${Routing.platformModule}/${Routing.platform.payAsYouGoPlan}`);
};

export const GoToPayAsYouGoOption = () => {
  const { planId } = useShallowEqualSelector((state: RootState) => ({
    planId: state?.user?.plan?.id,
  }));

  const navigate = useNavigate();
  const { t } = useTranslation();

  const hasPlanPayAsYouGo = planId === Plan.PAY_AS_YOU_GO;

  if (!hasPlanPayAsYouGo) {
    return null;
  }

  return <p onClick={goToUsers(navigate)}>{t("menu.pay_as_you_go")}</p>;
}