import { CircularProgress, MenuItem } from "@mui/material";
import { PlatformButton, PlatformModal } from "components";
import { get } from "lodash";
import { CreditCard } from "models";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import { toast } from "react-toastify";
import { userService } from "services";
import Styles from "./index.module.css";

interface Props {
  creditCard: CreditCard;
  handleCloseParent: () => void;
  loadCreditCards: () => Promise<void>;
}

export const DeleteButton = ({ creditCard, handleCloseParent, loadCreditCards }: Props) => {
  const { t } = useTranslation();
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const close = () => {
    setIsOpen(false);
    handleCloseParent();
  };

  const deleteCreditCard = async () => {
    if (isLoading) return;
    try {
      setIsLoading(true);
      await userService.deleteCreditCard(creditCard.id);
      await loadCreditCards();
      setIsLoading(false);
      close();
    } catch (e) {
      const message = get(e, "response.data.message");
      toast.error(t(message));
    } finally {
      setIsLoading(false);
    }
  };

  let options = (
    <>
      <PlatformButton onClick={close} label={t("common.cancel")} />
      <PlatformButton onClick={deleteCreditCard} label={t("payment_methods.delete_credit_card")} />
    </>
  );

  if (isLoading) {
    options = <CircularProgress />;
  }

  return (
    <>
      <MenuItem onClick={() => setIsOpen(true)}>{t("payment_methods.delete_credit_card")}</MenuItem>
      <PlatformModal isOpen={isOpen}>
        <p className="platformMessage">
          {t("payment_methods.are_you_sure_you_want_to_delete_the_credit_card")}
          {`****-****-****-${creditCard.maskedNumber}`} ?
        </p>
        <div className={Styles.buttonContainer}>{options}</div>
      </PlatformModal>
    </>
  );
};
