import { Button, Dialog, DialogActions, DialogTitle } from "@mui/material";
import { Action } from "@reduxjs/toolkit";
import { environment } from "environment";
import { useTranslation } from "react-i18next";
import { Routing } from "routing";
import { eventService } from "services";
import { ActionType } from "../../reducer";

interface Props {
  open: boolean;
  dispatch: React.Dispatch<Action<ActionType>>;
  createEvent: () => Promise<void>;
}

export const DeleteEvent = ({ createEvent, dispatch, open }: Props) => {
  const { t } = useTranslation();

  const recreateEvent = async () => {
    await eventService.deleteEvent();
    await createEvent();
    dispatch({ type: ActionType.CLEAR_DIALOG })
  };

  const cancel = () => dispatch({ type: ActionType.CLEAR_DIALOG });

  const goToEvent = () =>
    window.location.replace(
      `${environment.platformUrl}/${Routing.platformModule}/${Routing.platform.adminEvent}`,
    );

  return (
    <Dialog open={open}>
      <DialogTitle>{t("errors.another_event_is_running")}</DialogTitle>
      <DialogActions>
        <Button onClick={goToEvent}>{t("events.go_event")}</Button>
        <Button onClick={cancel}>{t("common.no")}</Button>
        <Button onClick={recreateEvent} autoFocus>
          {t("common.yes")}
        </Button>
      </DialogActions>
    </Dialog>
  );
};
