import { environment } from "environment";
import { Event } from "models";
import { deleteHttp, get, patch, post } from "utils";

class EventService {
  createEvent() {
    return post(`${environment.rooms.event}`);
  }

  getEvent() {
    return get(`${environment.rooms.event}`);
  }

  getRoomSummary() {
    return get(`${environment.rooms.eventRoomSummary}`);
  }

  getEventRoom(eventId: string) {
    return get(`${environment.rooms.event}/${eventId}`);
  }

  getRoomFromShuffle(eventId: string, roomId: string) {
    return post(`${environment.rooms.event}/${eventId}/shuffle`, { pin: roomId });
  }

  updateEvent(values: Partial<Event>) {
    return patch(`${environment.rooms.event}`, values);
  }

  deleteEvent() {
    return deleteHttp(`${environment.rooms.event}`);
  }
}

export const eventService = new EventService();
