import { Action } from "@reduxjs/toolkit";
import { get } from "lodash";
import { Room } from "models";
import { TFunction } from "react-i18next";
import { toast } from "react-toastify";
import { Routing } from "routing";
import { roomService } from "services";
import { ActionType } from "../reducer";

export const createNewRoom = async (
  t: TFunction<"translation", undefined>,
  dispach: React.Dispatch<Action<ActionType>>,
  name: string,
  type: string,
) => {
  dispach({ type: ActionType.IS_LOADING });
  try {
    const response = await roomService.createRoom(name, type);
    const room: Room = response.data;
    window.location.replace(`/${Routing.platformModule}/${Routing.platform.room}/${room.pin}`);
  } catch (e: unknown) {
    const message = get(e, "response.data.message");
    switch (message) {
      case "user_has_an_active_room":
        dispach({ type: ActionType.OPEN_ROOM_ERROR_DIALOG });
        break;
      default:
        toast.error(t(message));
        break;
    }
  } finally {
    dispach({ type: ActionType.IS_NOT_LOADING });
  }
};
