import { Action } from "@reduxjs/toolkit";
import { UserConfigurationButtonWrapper } from "hoc";
import { Reducer, useReducer } from "react";
import { useTranslation } from "react-i18next";
import { ButtonsContainer, CreateRoomForm, DeleteEvent, DeleteRoom } from "./components";
import { ActionType, initialState, reducer, State } from "./reducer";
import { createNewEvent } from "./utils";

export const CreateRoom = () => {
  const { t } = useTranslation();
  const [state, dispatch] = useReducer<Reducer<State, Action<ActionType>>>(reducer, initialState);
  const { isLoading, openEventErrorDialog, openRoomErrorDialog, openCreateRoomDialog } = state;

  return (
    <UserConfigurationButtonWrapper>
      <ButtonsContainer dispatch={dispatch} isLoading={isLoading} />
      <CreateRoomForm open={openCreateRoomDialog} dispatch={dispatch} />
      <DeleteRoom open={openRoomErrorDialog} dispatch={dispatch} />
      <DeleteEvent
        createEvent={createNewEvent(t, dispatch)}
        open={openEventErrorDialog}
        dispatch={dispatch}
      />
    </UserConfigurationButtonWrapper>
  );
};
