import { Plan } from "enums";
import { useTranslation } from "react-i18next";
import { NavigateFunction, useNavigate } from "react-router-dom";
import { Routing } from "routing";
import { useShallowEqualSelector, RootState } from "store";

const goToUsers = (navigate: NavigateFunction) => () => {
  navigate(`/${Routing.platformModule}/${Routing.platform.userPlanManagement}`);
};

export const GoToUsersOption = () => {
  const { planId } = useShallowEqualSelector((state: RootState) => ({
    planId: state?.user?.plan?.id,
  }));

  const navigate = useNavigate();
  const { t } = useTranslation();

  const hasPlanWithUsers = [Plan.ENTERPRISE, Plan.STANDARD].includes(planId as number);

  if (!hasPlanWithUsers) {
    return null;
  }

  return <p onClick={goToUsers(navigate)}>{t("menu.users")}</p>;
};
